module.exports = "<?php // {\"autorun\":true, \"persist\":false, \"single-expression\": false, \"render-as\": \"html\"}\n\n$stdErr = fopen('php://stderr', 'w');\n\nset_error_handler(function(...$args) use($stdErr, &$errors){\n\tfwrite($stdErr, print_r($args,1));\n});\n\n$docroot = '/persist';\n\nunlink($docroot);\nmkdir($docroot, 0777, true);\n\n$zip = new ZipArchive;\n\nif($zip->open('/persist/restore.zip', ZipArchive::RDONLY) === TRUE)\n{\n\t$total = $zip->count();\n\n\tfor($i = 0; $i < $total; $i++)\n\t{\n\t\t$zip->extractTo('/', $zip->getNameIndex($i));\n\t\tprint(1+$i / $total);\n\t}\n}\n\n$zip->close();\n\nunlink('/persist/restore.zip');\n\nexit;\n\n\n"